export default "@import url(\"https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.14.0/css/all.min.css\");\n@import url(\"https://cdnjs.cloudflare.com/ajax/libs/material-design-iconic-font/2.2.0/css/material-design-iconic-font.min.css\");\n@import url(\"https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap\");\n@import url(\"https://fonts.googleapis.com/css2?family=Oswald:wght@200;300;400;500;600;700&display=swap\");\nhtml, body {\n  display: block;\n  height: 100%;\n  margin: 0;\n  font-family: \"Poppins\", sans-serif;\n  position: relative;\n  z-index: 1;\n  background-color: #ffffff;\n}\n*, ::after, ::before {\n  box-sizing: border-box;\n}\nh1, h2, h3, h4, h5, h6 {\n  font-family: \"Oswald\", sans-serif;\n}\nimg {\n  max-width: 100%;\n  height: auto;\n}\n.container {\n  box-sizing: border-box;\n  padding: 0px 30px;\n  width: 100%;\n}\n.w-100 {\n  width: 100%;\n  box-sizing: border-box;\n}\n/*------ login css start -----*/\n.product_main_area {\n  width: 100%;\n  height: 100vh;\n  box-sizing: border-box;\n  padding: 0 0;\n  margin: 0 0;\n  position: relative;\n  z-index: 1;\n}\n.product_main_area:before {\n  content: \"\";\n  position: absolute;\n  top: 0;\n  left: 0;\n  width: 100%;\n  height: 190px;\n  box-sizing: border-box;\n  background-size: 100%;\n  background-repeat: no-repeat;\n  background-position: top center;\n  background-image: url('bg3.png');\n  z-index: -1;\n}\n.product_main_area:after {\n  content: \"\";\n  position: absolute;\n  bottom: 0;\n  left: 0;\n  width: 100%;\n  height: 140px;\n  box-sizing: border-box;\n  background-size: cover;\n  background-repeat: no-repeat;\n  background-position: top center;\n  background-image: url('beforeimg.jpg');\n  z-index: -1;\n}\n.product_main_area .product_body_area {\n  width: 100%;\n  box-sizing: border-box;\n  padding: 30px 0px;\n  margin: 0 0;\n  position: relative;\n}\n.product_main_area .product_body_area .header_area {\n  width: 100%;\n  box-sizing: border-box;\n  padding: 0 0;\n  margin: 0 0;\n  min-height: 52px;\n}\n.product_main_area .product_body_area .header_area .logo {\n  width: 90px;\n  box-sizing: border-box;\n  float: left;\n}\n.product_main_area .product_body_area .header_area .logo img {\n  width: 100%;\n  object-fit: cover;\n}\n.product_main_area .product_body_area .search_box_area {\n  width: 100%;\n  box-sizing: border-box;\n  margin-bottom: 25px;\n}\n.product_main_area .product_body_area .search_box_area h3 {\n  width: 100%;\n  box-sizing: border-box;\n  padding: 0 0;\n  margin: 0 0 6px;\n  font-size: 20px;\n  color: #fff;\n  font-weight: 400;\n  text-transform: capitalize;\n  letter-spacing: 1px;\n}\n.product_main_area .product_body_area .search_box_area .product_type_box {\n  width: 100%;\n  box-sizing: border-box;\n  padding: 15px 15px 5px 15px;\n  background: #783968;\n  background: linear-gradient(0deg, rgba(120, 57, 104, 0) 0%, white 100%);\n  border-radius: 5px;\n}\n.product_main_area .product_body_area .search_box_area .product_type_box .form-group {\n  width: 100%;\n  box-sizing: border-box;\n  padding: 0 0;\n  margin: 0 0 15px;\n  position: relative;\n  opacity: 0.6;\n}\n.form-group.active {\n  opacity: 1 !important;\n}\n.product_main_area .product_body_area .search_box_area .product_type_box .form-group span.iconbox {\n  position: absolute;\n  z-index: 1;\n  left: 9px;\n  top: 35px;\n  border-right: 1px solid rgba(235, 153, 41, 0.52);\n  padding-right: 6px;\n}\n.product_main_area .product_body_area .search_box_area .product_type_box .form-control {\n  width: 100%;\n  height: 45px;\n  box-sizing: border-box;\n  border-radius: 0;\n  padding: 0 15px 0 50px;\n  outline: none;\n  border-color: #eb9929;\n  background-color: #ffffff;\n}\n.product_main_area .product_body_area .search_box_area .product_type_box textarea.form-control {\n  height: 110px;\n  border-radius: 10px;\n}\n.product_main_area .product_body_area .search_box_area .product_type_box .btn {\n  width: 100%;\n  padding: 15px 20px;\n  text-transform: uppercase;\n  color: #fff;\n  background: #eb9929;\n  border: 1px solid #eb9929;\n  border-radius: 0;\n  outline: none;\n  max-width: 90%;\n  margin: auto 5%;\n}\n.product_main_area .product_body_area .search_box_area .product_type_box .form-group label {\n  width: 100%;\n  display: block;\n  margin-bottom: 5px;\n  font-size: 16px;\n  position: relative;\n  padding-left: 0;\n  font-family: \"Oswald\", sans-serif;\n}\n.product_main_area .product_body_area .search_box_area .product_type_box .form-group label .icon {\n  content: \"\";\n  position: absolute;\n  top: 2px;\n  left: 10px;\n  width: 20px;\n  height: 20px;\n  overflow: hidden;\n}\n.product_main_area .product_body_area .search_box_area .product_type_box .form-group label .icon img {\n  width: 100%;\n  height: 100%;\n  object-fit: cover;\n}\n.nav_bar {\n  float: left;\n  background: transparent;\n  color: #fff;\n  padding: 0 0;\n  margin: 0 0;\n  border: none;\n  margin-right: 12px;\n  font-size: 24px;\n  margin-top: 3px;\n  outline: none;\n}\n.product_main_area .product_body_area .search_box_area .product_type_box h3 {\n  width: 100%;\n  display: block;\n  margin-bottom: 12px;\n  text-transform: capitalize;\n  color: #eb9929;\n  position: relative;\n  padding-bottom: 12px;\n  border-bottom: 1px solid rgba(0, 0, 0, 0.13);\n}\n.product_main_area .product_body_area .search_box_area .product_type_box img.user_icon {\n  content: \"\";\n  position: absolute;\n  top: 2px;\n  left: 0;\n  width: 28px;\n  height: 28px;\n  object-fit: cover;\n}\n/*# sourceMappingURL=data:application/json;base64, */";